import React, { Component } from 'react';

import Dashboard from './pages/Dashboard';
import Login from './pages/Login';
import Forgot from './pages/Forgot-password';
import Register from './pages/Register'
import Error from './pages/404'
import Broken from './components/BrokenLinks'
import Fixed from './components/FixedLinks'
import Ignored from './components/IgnoredLinks';
import Websites from './components/Websites';
import Pages from './components/Pages';
import Riks from './components/Risk';

import { BrowserRouter } from 'react-router-dom'
import { Route, Link } from 'react-router-dom'

import  CustomersList from './CustomersList'
import  CustomerCreateUpdate  from './CustomerCreateUpdate'
import './App.css';

const BaseLayout = () => (
  <div className="container-fluid">
<nav className="navbar navbar-expand-lg navbar-light bg-light">
  <a className="navbar-brand" href="#">Django React Demo</a>
  <button className="navbar-toggler" type="button" data-toggle="collapse" data-target="#navbarNavAltMarkup" aria-controls="navbarNavAltMarkup" aria-expanded="false" aria-label="Toggle navigation">
    <span className="navbar-toggler-icon"></span>
  </button>
  <div className="collapse navbar-collapse" id="navbarNavAltMarkup">
    <div className="navbar-nav">
      <a className="nav-item nav-link" href="/">CUSTOMERS</a>
      <a className="nav-item nav-link" href="/customer">CREATE CUSTOMER</a>
      
    </div>
  </div>
</nav>  

    <div className="content">
      <Route path="/" exact component={CustomersList} />
      <Route path="/customer/:pk"  component={CustomerCreateUpdate} />
      <Route path="/customer/" exact component={CustomerCreateUpdate} />

    </div>

  </div>
)

class App extends Component {
  render() {
    return (
      
	<BrowserRouter>
	    <Route exact path='/login' component= {Login}/>
	    <Route exact path='/main' component= {Dashboard}/>
	    <Route exact path='/forgot' component= {Forgot}/>
	    <Route exact path='/register' component= {Register}/>
	    <Route exact path='/broken' component= {Broken}/>
	    <Route exact path='/fixed' component= {Fixed}/>
	    <Route exact path='/ignored' component= {Ignored}/>
	    <Route exact path='/pages' component= {Pages}/>
	    <Route exact path='/websites' component= {Websites}/>
	    <Route exact path='/risk' component= {Riks}/>
	    <Route exact path='/' component= {BaseLayout}/>
      <Route exact path='/customer' component= {CustomerCreateUpdate}/>
 	 </BrowserRouter>
    );
  }
}

export default App;
