import React from 'react';
import { Link } from 'react-router-dom';

const Forgot = () => {
    return ( 
        <div class="container">
        <div class="row justify-content-center">
            <div class="col col-md-6">
                <div class="card  my-5 shadow-sm shadow-hover">
                    <div class="card-body p-0 ">
                        <div class="row">
                            <div class="col">
                                <div class="p-5">
                                    <div class="text-center">
                                        
                                        <h4 class="text-dark mb-2">Forgot Your Password?</h4>
                                        <p class="mb-4">We get it, stuff happens. Just enter your email address below and we'll send you a link to reset your password!</p>
                                    </div>
                                    <form class="user">
                                        <div class="form-group"><input class="form-control form-control-user " type="email" id="exampleInputEmail" aria-describedby="emailHelp" placeholder="Enter Email Address..." name="email"/>
                                        </div>
                                        <button class="btn btn-success btn-block text-white btn-user"
                                            type="submit">Reset Password</button></form>
                                    <div class="text-center">
                                        <hr/><Link class="small text-dark" to="/register">Create an Account!</Link>
                                        </div>
                                    <div class="text-center ">
                                        <Link class="small text-dark" to="/">Already have an account? Login!</Link>
                                        </div>
                                </div>
                            </div>
                        </div>
                    </div>
                </div>
            </div>
        </div>
    </div>
     );
}
 
export default Forgot;