import React, { Component } from 'react';

import CustomersService from './CustomersService';

const customersService = new CustomersService();

class CustomerCreateUpdate extends Component {
    constructor(props) {
        super(props);
    
        //this.handleChange = this.handleChange.bind(this);
        this.handleSubmit = this.handleSubmit.bind(this);
      }

      componentDidMount(){
        const { match: { params } } = this.props;
        if(params && params.pk)
        {
          customersService.getCustomer(params.pk).then((c)=>{
            this.refs.enabled.value = c.enabled;
            this.refs.website.value = c.website;
            this.refs.url.value = c.url;
            
          })
        }
      }

      handleCreate(){
        customersService.createCustomer(
          {
            "enabled": this.refs.enabled.value,
            "website": this.refs.website.value,
            "url": this.refs.url.value,
            
        }          
        ).then((result)=>{
          alert("Customer created!");
        }).catch(()=>{
          alert('There was an error! Please re-check your form.');
        });
      }
      handleUpdate(pk){
        customersService.updateCustomer(
          {
            "pk": pk,
            "enabled": this.refs.enabled.value,
            "website": this.refs.website.value,
            "url": this.refs.url.value,
        }          
        ).then((result)=>{
          console.log(result);
          alert("Customer updated!");
        }).catch(()=>{
          alert('There was an error! Please re-check your form.');
          console.log(this.result);
        });
      }
      handleSubmit(event) {
        const { match: { params } } = this.props;

        if(params && params.pk){
          this.handleUpdate(params.pk);
        }
        else
        {
          this.handleCreate();
        }

        event.preventDefault();
      }
    
      render() {
        return (
          <form onSubmit={this.handleSubmit}>
          <div className="form-group">
            <label>
              Enabled:</label>
              <input className="form-control" type="text" ref='enabled' />
            
            <label>
              Website:</label>
              <input className="form-control" type="text" ref='website'/>
            
            <label>
              Url:</label>
              <input className="form-control" type="text" ref='url' />
            
            

            <input className="btn btn-primary" type="submit" value="Submit" />
            </div>
          </form>
        );
      }  
}

export default CustomerCreateUpdate;