import React, { Fragment } from 'react';
import Icon from '../img/icon.png';
import { Link } from 'react-router-dom';


const Login = () => {
    return ( 
        
       
        <div class="container">
        <div class="row justify-content-center">
            <div class="col-md-9 col-lg-12 col-xl-10">
                <div class="card shadow-sm shadow-hover o-hidden border-0 my-5">
                    <div class="card-body p-0">
                        <div class="row">
                            <div class="col-lg-6 d-none d-lg-flex">
                                <div class="col  shadow-sm shadow-hover">
                                <div class="card-header bg-white py-3 h-25">
                                    <a href="/main">go normal</a>
                                    <Link to='/main' className="">    go react</Link> 
                                     <h2 class="text-muted font-weight-bold m-0 s-1">Projects</h2>
                                     
                                </div>
                                <div class="card-body pt-5">
                                    <h4 class="small font-weight-bold">Server migration<span class="float-right">20%</span></h4>
                                    <div class="progress mb-4">
                                        <div class="bar-one progress-bar-striped progress-bar-animated bg-danger" aria-valuenow="20" aria-valuemin="0" aria-valuemax="100" ><span class="sr-only">20%</span></div>
                                    </div>
                                    <h4 class="small font-weight-bold">Sales tracking<span class="float-right">40%</span></h4>
                                    <div class="progress mb-4">
                                        <div class="bar-two progress-bar-striped progress-bar-animated bg-warning" aria-valuenow="40" aria-valuemin="0" aria-valuemax="100" ><span class="sr-only">40%</span></div>
                                    </div>
                                    <h4 class="small font-weight-bold">Customer Database<span class="float-right">60%</span></h4>
                                    <div class="progress mb-4">
                                        <div class="bar-three progress-bar-striped progress-bar-animated bg-primary" aria-valuenow="60" aria-valuemin="0" aria-valuemax="100" ><span class="sr-only">60%</span></div>
                                    </div>
                                    <h4 class="small font-weight-bold">Payout Details<span class="float-right">80%</span></h4>
                                    <div class=" progress mb-4">
                                        <div class="bar-four progress-bar-striped progress-bar-animated bg-info" aria-valuenow="80" aria-valuemin="0" aria-valuemax="100"><span class="sr-only">80%</span></div>
                                    </div>
                                    <h4 class="small font-weight-bold">Account setup<span class="float-right">Complete!</span></h4>
                                    <div class="progress mb-4">
                                        <div class="bar-five progress-bar-striped progress-bar-animated bg-success" aria-valuenow="100" aria-valuemin="0" aria-valuemax="100"><span class="sr-only">100%</span></div>
                                    </div>
                                </div>
                                     
                                    
                                </div>    
                            </div>
                            <div class="col-lg-6 shadow-muted">
                                <div class="p-5 ">
                                     <div className="col-12 user-img">
                                        <img src={Icon} alt="" className="d-block mx-auto "></img>
                                     </div>
                                    <form class="user pt-3">
                                    <div class="form-group"><input class="form-control form-control-user" type="email" id="exampleInputEmail" aria-describedby="emailHelp" placeholder="Enter Email Address..." name="email"/></div>
                                    <div class="form-group"><input class="form-control form-control-user" type="password" id="exampleInputPassword" placeholder="Password" name="password"/></div>
                                    <div class="form-group">
                                            <div class="custom-control custom-checkbox small"></div>
                                        </div><button class="btn btn-success btn-block text-white btn-user" type="submit">Login</button>
                                        <hr></hr>   
                                    </form>
                                    
                                    <div className="row forgot">
                                        <div className="col-6 ">
                                            <Link to="/forgot" className="btn btn-outline-success col-12 ">
                                                <i className="fas fa-question"></i> Forgot Password </Link>
                                        </div>
                                        <div className="col-6">
                                            <Link to="/register" className="btn btn-outline-success col-12 ">
                                                <i className="fas fa-user-plus"></i> Create account </Link>
                                                
                                        </div>
                                    </div>
                                      
                                </div>
                            </div>
                        </div>
                    </div>
                </div>
            </div>
        </div>
    </div>
        
     );
}
 
export default Login;